import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ApiProvider} from "../../providers/api/api";
import {HomePage} from "../home/home";

/**
 * Generated class for the PharmahomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-pharmahome',
  templateUrl: 'pharmahome.html',
})
export class PharmahomePage {

  pharm: any = {};
  officines: any[];
    fofficines: any[];
  towns: any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, public api: ApiProvider) {
      this.pharm = this.api.user ? this.api.user : {};
    this.officines = this.api.officines;
    this.towns = this.api.towns;
      this.fofficines = [];
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PharmahomePage');
  }

  register() {
    this.api.registerPharm(this.pharm);
  }

  cancel() {
    this.pharm = {};
    this.navCtrl.setRoot(HomePage)
  }

  gohome() {
    this.navCtrl.setRoot(HomePage)
  }

    filterOfficines(city) {
        let x = this.officines.filter((item: any) => {
            return (item.commune.toLowerCase().indexOf(city.toLowerCase()) > -1);
        });

        this.fofficines = x;
    }
}



